<template>
	<view class='container padding'>
		<view class="list" v-if="!empty">
			<view class="item padding" v-for="(item,index) in listData" :key="index" @click="goSelect(item)">
				<view class="row flex-row-between">
					<view class='title'>{{item.carTypeName}}</view>
					<view class="status flex-row-end">
						<text class="txt">
							{{item.awardClaimed==0?'待领取':item.awardClaimed==1?'领取中':item.awardClaimed==2?'已领取':'/'}}
						</text>
						<image src="../../static/right_black.png" class="icon"></image>
					</view>
				</view>
				<view class="time">{{item.createTimeStr}}</view>
			</view>
			<loadMore :status="status" :content-text="textDesc"></loadMore>
		</view>
		<view class="no_data" v-else>
			暂无数据
		</view>
	</view>
</template>

<script>
	import loadMore from "../../components/loadMore/loadMore.vue"
	import {formatTime} from "../../common/tools.js"
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"noMore", //可选值：more（loading前）、loading（loading中）、noMore（没有更多了
				empty:false,
				listData:[],
				paramsInfo:{
					staffId:'',// 
					pageNum:1,
					pageSize:10
				},
				userType:1 // 1 管理员，2 员工，3 客户
				//奖励领取状态(0:未领取,1:领取中,2:已领取)
			};
		},
		computed:{
			currentUser(){
				let _userInfo=uni.getStorageSync('userInfo');
				if(_userInfo){
					return JSON.parse(_userInfo)
				}else{
					return null
				}
			}
		},
		methods:{
			goSelect(item){
				if(item.awardClaimed==0){ //待领取或领取中
					this.perfectNavigateTo({
						url:'/pages/award/selectGift?orderId='+item.orderIdStr
					})
				}else{ //已领取
					this.perfectNavigateTo({
						url:'/pages/award/lookGift?orderId='+item.orderIdStr
					})
				}
			},
			getList(){
				var _requestUrl='/orderAward/staff/sendAwardOrders'
				if(this.userType==3){ // 客户
					_requestUrl='/orderAward/queryClaimAwardOrders'
					delete this.paramsInfo.staffId
				}
				this.request(_requestUrl,this.paramsInfo).then((res)=>{
					if(res.resultCode==0){
						let _data=res.data.list
						for(let i=0;i<_data.length;i++){
							_data[i].createTimeStr=formatTime(_data[i].createTime)
						}
						if(_data.length>0){ //如果有数据
							this.empty=false
							this.listData=this.listData.concat(_data)
							if(_data.length<this.paramsInfo.pageSize){ //如果有数据但是 小于 每页数量
								this.status="noMore"
							}else{
								this.paramsInfo.pageNum++
								this.status="more"
							}
						}else{ //如果这次没数据
							this.empty=false
							this.status="noMore"
							if(this.paramsInfo.pageNum==1){ //说明开始就没数据
								this.empty=true
							}
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
					uni.stopPullDownRefresh()
				})
			},
			
		},
		onPullDownRefresh(){ //下拉刷新
			this.paramsInfo.pageNum=1
			this.listData=[]
			this.getList()
		},
		onReachBottom(){ //加载更多
			if(this.status!="noMore"){
				this.getList()
			}
		},
		
		components:{
			loadMore
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
		onShow(){
			this.paramsInfo.staffId=this.currentUser.userId
			uni.startPullDownRefresh();
		},
		onLoad(params){
			this.userType=params.userType
		}
	}
</script>

<style lang="stylus">
	.container
		.list
			.item
				background:rgba(255,255,255,1);
				box-shadow:0upx 0upx 20upx 0upx rgba(0,0,0,0.05);
				border-radius:16upx;
				&:not(:first-child)
					margin-top:40upx
				.title
					font-size:28upx;
					font-weight:bold;
					color:rgba(30,30,30,1);
					flex:1
				.status
					width:120upx
					.icon
						width:28upx
						height:28upx
						margin-left:20upx
					.txt
						font-size:24upx;
						font-weight:500;
						color:rgba(30,30,30,1);
				.time
					font-size:24upx
					color:#8491A8
					margin-top:20upx
						
</style>
